import React from 'react';
import { useHistory } from 'react-router-dom';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Fade from '@material-ui/core/Fade';
import IconButton from '@material-ui/core/IconButton';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import Box from '@material-ui/core/Box';
import { onBlockUser } from 'redux/actions';
import { useDispatch } from 'react-redux';
import { Snackbar, SnackbarContent } from '@material-ui/core';

interface TableActionProps {
  uid: string;
  blocked: boolean;
}

const TableActions: React.FC<TableActionProps> = ({ uid, blocked }) => {
  const [openCopiedSnackbar, setOpenCopiedSnackbar] = React.useState(false);
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const history = useHistory();

  const dispatch = useDispatch();

  const open = Boolean(anchorEl);

  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleViewUser = () => {
    history.push('/users/view/' + uid);
    setAnchorEl(null);
  };

  const handleBlockUser = (blocked: boolean) => {
    // if (confirm("Do you want to block this user?")) {
    dispatch(onBlockUser(uid, !blocked));
    // }
    setAnchorEl(null);
  };

  const handleClose = () => {
    setAnchorEl(null);
  }

  const onCloseSnackbarHandler = (_event: any, _reason: any) => {
    setOpenCopiedSnackbar(false);
  }

  return (
    <Box>
      <IconButton
        aria-controls='fade-menu'
        aria-haspopup='true'
        onClick={handleClick}>
        <MoreVertIcon />
      </IconButton>
      <Menu
        id='fade-menu'
        anchorEl={anchorEl}
        keepMounted
        open={open}
        onClose={handleClose}
        TransitionComponent={Fade}>
        <MenuItem style={{ fontSize: 14 }} onClick={handleViewUser}>
          View User
        </MenuItem>
        <MenuItem style={{ fontSize: 14 }} onClick={() => { handleBlockUser(blocked) }}>
          {blocked ? 'Unblock User' : 'Block User'}
        </MenuItem>
      </Menu>
      <Snackbar
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        open={openCopiedSnackbar}
        autoHideDuration={6000}
        onClose={onCloseSnackbarHandler}>
        <SnackbarContent
          className='success'
          aria-describedby='client-snackbar'
          message={
            <span id='client-snackbar'>
              Blocked User
            </span>
          }
        />
      </Snackbar>
    </Box>
  );
};
export default TableActions;
