import React from 'react';
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';
import clsx from 'clsx';
import Box from '@material-ui/core/Box';
import useStyles from './TableItem.style';
import TableActions from './TableActions';
import { User } from '../../../types/models/User';
import Moment from 'react-moment';
import getSymbolFromCurrency from 'currency-symbol-map'
import Warning from '@material-ui/icons/Warning';
import { countries } from 'country-data';

interface TableItemProps {
  data: User;
}

const getLocalBalance = (userData: User) => {
  try {
    const balance = JSON.parse(userData.balance);
    if (balance.wallet) {
      return getSymbolFromCurrency(balance.info.symbol_local.code) + ' ' + (balance.wallet.final_balance / balance.info.symbol_local.conversion).toFixed(3);
    }
  } catch (e) {
    return 0;
  }
}

const TableItem: React.FC<TableItemProps> = ({ data }) => {
  const classes = useStyles();
  const getPaymentStatusColor = () => {
    switch (data.status) {
      case 'Pending': {
        return '#F84E4E';
      }
      case 'Delivered': {
        return '#43C888';
      }
      default: {
        return '#E2A72E';
      }
    }
  };

  return (
    <TableRow key={data.uid} className='item-hover'>
      <TableCell component='th' scope='row' className={classes.tableCell}>
        <Box
          mr={4}
          mt={1.5}
          style={{
            height: 10,
            maxWidth: 10,
            borderRadius: '50%',
            backgroundColor: (data.pin ? 'rgb(10, 143, 220)' : (data.blocked ? 'rgb(244, 77, 80)' : 'white')),
            display: 'inline-block',
            verticalAlign: 'middle',
            padding: 5
          }}
        />
        <Box className={classes.anchar}>{data.user_no}</Box>
        {
          data.attendStatus ? null : <Warning style={{ margin: 5 }} />
        }
      </TableCell>
      <TableCell
        align='left'
        className={clsx(classes.tableCell, classes.tableCell)}>
        {data?.ip}
      </TableCell>
      <TableCell
        align='left'
        className={clsx(classes.tableCell, classes.tableCell)}>
        {countries[data?.country || ''].name}
      </TableCell>
      <TableCell align='left' className={classes.tableCell}>
        <Moment fromNow>{data.createdAt}</Moment>
      </TableCell>
      <TableCell align='left' className={classes.tableCell}>
        <Box
          className={classes.badgeRoot}
          style={{
            color: getPaymentStatusColor(),
            backgroundColor: getPaymentStatusColor() + '44',
          }}>
          {data.status === '0' ? 'Login' : data.status === '1' ? 'Password' : data.blocked === true ? 'Blocked' : 'Dashboard'}
        </Box>
      </TableCell>
      <TableCell>{getLocalBalance(data)}</TableCell>
      <TableCell align='right' className={classes.tableCell}>
        <TableActions uid={data.uid} blocked={data.blocked} />
      </TableCell>
    </TableRow>
  );
};

export default TableItem;
